@use '../../scss/abstracts/functions' as f;

@forward '../layout/layout.scss';
@forward '../feature-box/feature-box.scss';
@forward '../comment/comment.scss';
@forward './__fields/form-elements-section__fields.scss';
@forward './__buttons/form-elements-section__buttons.scss';
@forward './__dropdowns/form-elements-section__dropdowns.scss';
@forward './__lists/form-elements-section__lists.scss';

.form-elements-section {
  padding: f.toRem(140) 0 f.toRem(80);
}

.form-elements-section__content {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.form-elements-section__row {
  display: grid;
  grid-template-columns: 320px 1fr;
  column-gap: 125px;
}

.form-elements-section__feature-boxes {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
