@forward "../card/card";
@forward "../field/field";
@forward "../text-field/text-field";
@forward "../radio-button/radio-button";
@forward "../form-unit-header/form-unit-header";
@forward "../masked-text-field/masked-text-field";
@forward "../toggle/toggle";
@forward "../button/button";

.card-registration_size_responsive {
  width: 100%;
  max-width: 100%;

  .field {
    width: 100%;
  }

  .card-registration__login-btn {
    width: 100%;
  }
}

.card-registration__title {
  font: var(--type-heading-h1);
  color: var(--color-dark-shade-100);
}

.card-registration__form {
  margin-top: 20px;
}

.card-registration__form > *:not(:first-child) {
  margin-top: 20px;
}

.card-registration__personal-info {
}

.personal-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.personal-info__sex {
  display: flex;
  gap: 20px;
}

.card-registration__birth-date {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.card-registration__authorization-data {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.card-registration__authorization-data-body {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.card-registration__login-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 30px;
  flex-wrap: wrap;
  row-gap: 10px;
}

.card-registration__login-label {
  font: var(--type-body);
  color: #000;
}
